import React, { useState, useEffect } from 'react';
import BpmControl from './BpmControl';
import audioEngine from './AudioEngine';
import './PlayerMenu.css';
import { FaDrum } from 'react-icons/fa'; //       npm install react-icons

const PlayerMenu = ({ bpm, onBpmChange }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [displaySignature, setDisplaySignature] = useState('4/4');
  //      
  const [metronomeEnabled, setMetronomeEnabled] = useState(false);
  const [swingValue, setSwingValue] = useState(0);

  //    time signature
  const updateTimeSignature = () => {
    const current = audioEngine.getTimeSignature();

    if (current !== null && current !== undefined) {
      let numerator, denominator;

      if (Array.isArray(current) && current.length >= 2) {
        numerator = current[0];
        denominator = current[1];
      } else if (typeof current === 'number') {
        numerator = current;
        denominator = 4; //  
      } else if (typeof current === 'string') {
        numerator = parseInt(current, 10);
        denominator = 4;
      } else {
        numerator = 4;
        denominator = 4;
      }

      setDisplaySignature(`${numerator}/${denominator}`);
    }
  };

  //    
  useEffect(() => {
    setIsPlaying(audioEngine.isPlaying);
    updateTimeSignature();
    //     
    setMetronomeEnabled(audioEngine.metronomeEnabled);
    setSwingValue(audioEngine.swingValue);

    const intervalId = setInterval(() => {
      updateTimeSignature();
    }, 200);

    return () => {
      clearInterval(intervalId);
    };
  }, []);

  const toggleBpmMenu = () => setIsMenuOpen(!isMenuOpen);
  const handleCloseMenu = () => setIsMenuOpen(false);

  const handleBpmChange = (newBpm) => {
    onBpmChange(newBpm);
    audioEngine.setBPM(newBpm);
  };

  const handlePlayToggle = async () => {
    try {
      const playing = await audioEngine.togglePlay();
      setIsPlaying(playing);
      updateTimeSignature();
    } catch (error) {
      console.error("   :", error);
    }
  };

  //  / 
  const handleToggleMetronome = () => {
    const isEnabled = audioEngine.toggleMetronome();
    setMetronomeEnabled(isEnabled);
  };

  //    
  const handleSwingChange = (e) => {
    const value = parseInt(e.target.value, 10);
    const actualValue = audioEngine.setSwing(value);
    setSwingValue(actualValue);
  };

  return (
    <div className="player-menu">
      <div className="menu-left">
        <button 
          className={`play-button ${isPlaying ? 'playing' : ''}`}
          onClick={handlePlayToggle}
        >
          {isPlaying ? '-' : '+'}
        </button>

        <div className="time-signature">
          {displaySignature}
        </div>

        {/*   */}
        <button 
          className={`metronome-button ${metronomeEnabled ? 'active' : ''}`}
          onClick={handleToggleMetronome}
          title="/ "
        >
          <FaDrum />
        </button>
      </div>

      <div className="menu-right">
        {/*   */}
        <div className="swing-control">
          <div className="swing-label">
            <span></span>
          </div>
          <input
            type="range"
            min="0"
            max="50"
            value={swingValue}
            onChange={handleSwingChange}
            className="swing-slider"
          />
          <span className="swing-value">{swingValue}%</span>
        </div>

        <button 
          className="bpm-toggle-button" 
          onClick={toggleBpmMenu}
        >
          {bpm} BPM
        </button>
      </div>

      {isMenuOpen && (
        <BpmControl 
          bpm={bpm} 
          onBpmChange={handleBpmChange} 
          onClose={handleCloseMenu}
        />
      )}
    </div>
  );
};

export default PlayerMenu;