import * as Tone from 'tone';
import instrumentsConfig from './instruments';

class AudioEngine {
  constructor() {
    this.isPlaying = false;
    this.bpm = 120;
    this.transport = Tone.Transport;
    this.transport.bpm.value = this.bpm;
    this.transport.timeSignature = [4, 4];
    this.sequences = [];
    this.currentBlockIndex = 0;
    this.totalBlocks = 0;
    this.instruments = {};

    //      
    this.metronomeEnabled = false;
    this.swingValue = 0; //  0  50
    this.metronome = null;
    this.metronomeVolume = new Tone.Volume(-10); //  

    //  
    this.initializeInstruments();
    this.initializeMetronome();
  }

  //   time signature
  getTimeSignature() {
    //    ,     time signature
    if (this.totalBlocks > 0) {
      return [this.totalBlocks, 4];
    }

    //   ,   transport time signature
    const ts = this.transport.timeSignature;

    if (ts === null || ts === undefined) {
      return [4, 4];
    }

    if (Array.isArray(ts)) {
      return ts;
    }

    if (typeof ts === 'number') {
      return [ts, 4];
    }

    return [4, 4];
  }

  //  
initializeMetronome() {
  //     
  this.metronome = new Tone.Synth({
    oscillator: { type: 'triangle' },
    envelope: { attack: 0.001, decay: 0.1, sustain: 0, release: 0.1 }
  }).toDestination();

  //    ( "")
  this.metronome.volume.value = -Infinity;
  this.normalMetronomeVolume = -10; //   

  //   
  this.metronomeEnabled = false;
  this.metronomeEvent = null;
}

// /  (  )
toggleMetronome() {
  this.metronomeEnabled = !this.metronomeEnabled;

  //       
  if (this.metronomeEnabled) {
    this.metronome.volume.value = this.normalMetronomeVolume;
  } else {
    this.metronome.volume.value = -Infinity; //  
  }

  //   ,      - 
  if (this.isPlaying && this.metronomeEvent === null) {
    this.startMetronome();
  }

  return this.metronomeEnabled;
}

//  
initializeMetronome() {
  //     
  this.metronome = new Tone.Synth({
    oscillator: { type: 'triangle' },
    envelope: { attack: 0.001, decay: 0.1, sustain: 0, release: 0.1 }
  }).toDestination();

  this.normalMetronomeVolume = -10;
  this.metronome.volume.value = -Infinity; //   
  this.metronomeEnabled = false;
  this.metronomeLoop = null;
}

// / 
toggleMetronome() {
  this.metronomeEnabled = !this.metronomeEnabled;

  //   
  if (this.metronomeEnabled) {
    this.metronome.volume.value = this.normalMetronomeVolume;
  } else {
    this.metronome.volume.value = -Infinity;
  }

  return this.metronomeEnabled;
}

//   -  Tone.Loop  scheduleRepeat
startMetronome() {
  //   loop,  
  this.stopMetronome();

  //   
  const timeSignature = this.getTimeSignature();
  const beatsPerBar = Array.isArray(timeSignature) ? timeSignature[0] : 4;

  //  ,     
  this.metronomeLoop = new Tone.Loop((time) => {
    //     
    const position = Tone.Transport.position;
    const splitPos = position.split(':');

    //       (: "::")
    // splitPos[0] = , splitPos[1] =   
    const beat = parseInt(splitPos[1]); 

    //    - ,  - 
    if (beat === 0) {
      this.metronome.triggerAttackRelease('C5', '16n', time);
    } else {
      this.metronome.triggerAttackRelease('G4', '16n', time);
    }
  }, '4n').start(0);
}

//  
stopMetronome() {
  if (this.metronomeLoop) {
    this.metronomeLoop.stop();
    this.metronomeLoop.dispose();
    this.metronomeLoop = null;
  }
}

//   
setMetronomeVolume(volume) {
  this.normalMetronomeVolume = Math.max(-40, Math.min(0, volume));

  if (this.metronomeEnabled) {
    this.metronome.volume.value = this.normalMetronomeVolume;
  }

  return this.normalMetronomeVolume;
}

  //   Swing
  setSwing(swingValue) {
    //    0  50
    this.swingValue = Math.max(0, Math.min(50, swingValue));
    this.updateSwing();
    return this.swingValue;
  }

  //   Swing
  updateSwing() {
    //    ,   Tone.js ( 0  1)
    const swingAmount = this.swingValue / 100 * 2; // 50   1
    Tone.Transport.swingSubdivision = '8n'; //    
    Tone.Transport.swing = swingAmount;

    //    ,   
    if (this.metronomePattern) {
      this.metronomePattern.playbackRate = 1;
    }
  }

  //   setBPM
  setBPM(bpm) {
    this.bpm = bpm;
    this.transport.bpm.value = bpm;
    //   ,   
    this.updateSwing();
  }

  //  
  async initializeInstruments() {
    const loadPromises = Object.entries(instrumentsConfig).map(([instrumentId, config]) => {
      return new Promise(resolve => {
        const urls = {};
        for (const note in config.urls) {
          urls[note] = config.urls[note];
        }

        const sampler = new Tone.Sampler({
          urls: urls,
          baseUrl: config.baseUrl,
          onload: () => {
            if (typeof config.volume === 'number') {
              sampler.volume.value = config.volume;
            }

            this.instruments[instrumentId] = sampler;
            resolve();
          }
        }).toDestination();
      });
    });

    //    
    await Promise.all(loadPromises);
  }

  //  
  getInstrument(instrumentId) {
    return this.instruments[instrumentId] || new Tone.Synth().toDestination();
  }

  //     
  prepareSequencerData(sequencerStructure, cellsState) {
    this.sequences = [];
    this.totalBlocks = sequencerStructure.length;

    //  time signature    
    this.transport.timeSignature = [this.totalBlocks, 4];
    console.log(`  ${this.totalBlocks}/4`); //  

    //    
    this.transport.cancel();

    sequencerStructure.forEach((block, blockIndex) => {
      const trackSchedule = {};

      if (block && block.trackSteps) {
        Object.entries(block.trackSteps).forEach(([trackName, stepCount]) => {
          trackSchedule[trackName] = [];

          //     
          for (let step = 0; step < stepCount; step++) {
            const cellId = `${blockIndex}-${trackName}-${step}`;
            const cell = cellsState[cellId];

            if (cell && cell.instrumentId) {
              //      
              const time = step / stepCount;

              trackSchedule[trackName].push({
                time,
                note: cell.note || 'C4',
                instrumentId: cell.instrumentId,
                duration: 1 / stepCount
              });
            }
          }
        });
      }

      this.sequences.push(trackSchedule);
    });

    //  
    this.scheduleBlocks();

    //   ,     time signature
    if (this.metronomeEnabled) {
      this.stopMetronome(); //  
      this.startMetronome(); //     
    }

    return true;
  }

  //   
  scheduleBlocks() {
    const blockDuration = '4n';
    let startTime = 0;

    this.sequences.forEach((blockTracks, blockIndex) => {
      Object.entries(blockTracks).forEach(([trackName, events]) => {
        events.forEach(event => {
          //   
          const absoluteTime = startTime + (event.time * Tone.Time(blockDuration).toSeconds());

          //    
          this.transport.schedule((time) => {
            const instrument = this.getInstrument(event.instrumentId);
            instrument.triggerAttackRelease(
              event.note,
              event.duration * Tone.Time(blockDuration).toSeconds(),
              time
            );
          }, absoluteTime);
        });
      });

      //     
      startTime += Tone.Time(blockDuration).toSeconds();
    });

    //  
    this.transport.loop = true;
    this.transport.loopEnd = startTime;
    this.transport.loopStart = 0;

    return true;
  }

  getPosition() {
    return this.transport.seconds;
  }

//   
async play() {
  try {
    await Tone.start();

    //       -    
    const wasPlaying = Tone.Transport.state === "paused";

    this.transport.start();
    this.isPlaying = true;

    //         
    //      
    if (!wasPlaying || !this.metronomeLoop) {
      this.startMetronome();
    }

    //  
    const visualScheduler = await import('./VisualScheduler').then(module => module.default);
    visualScheduler.setAudioEngine(this);
    visualScheduler.start();

    return true;
  } catch (error) {
    console.error("   :", error);
    return false;
  }
}

pause() {
  if (this.isPlaying) {
    this.transport.pause();
    this.isPlaying = false;

    //      -     

    //  
    const visualScheduler = require('./VisualScheduler').default;
    visualScheduler.pause();
  }
  return true;
}

stop() {
  this.transport.stop();
  this.isPlaying = false;

  //      
  this.stopMetronome();

  //  
  const visualScheduler = require('./VisualScheduler').default;
  visualScheduler.stop();

  return true;
}

  //  
  async togglePlay() {
    try {
      //       
      if (Tone.context.state !== 'running') {
        await Tone.start();
      }

      if (this.isPlaying) {
        this.pause();
      } else {
        await this.play();
      }
      return this.isPlaying;
    } catch (error) {
      console.error("   :", error);
      return false;
    }
  }
}

//    
const audioEngine = new AudioEngine();
export default audioEngine;